"use client";

import Image from "next/image";
import { ArrowRight } from "lucide-react";
import TypingAnimation from "@/components/typeing-animation";
// ⬆️ Make sure this path matches your actual file location

export default function HeroSection() {
    return (
        <>
            {/* Hero Section */}
            <section
                id="home"
                className="min-h-screen flex items-center pt-16 bg-gradient-to-br from-background via-background to-accent/10"
            >
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 w-full py-20">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-12 items-center">

                        {/* Left Side - Text */}
                        <div className="space-y-6">
                            <TypingAnimation />
                            <p className="text-xl text-muted-foreground leading-relaxed">
                                I build scalable web applications using modern technologies.
                            </p>

                            <a
                                href="#contact"
                                className="inline-flex items-center gap-2 px-8 py-3 bg-gradient-to-r from-orange-500 
                to-pink-500 hover:from-orange-600 hover:to-pink-600 text-white font-semibold rounded-lg 
                transition-all hover:shadow-lg"
                            >
                                Get In Touch <ArrowRight className="w-5 h-5" />
                            </a>
                        </div>

                        {/* Right Side - Image */}
                        <div className="relative h-96 md:h-full">
                            <Image
                                src="/software-developer-working.png"
                                alt="Developer"
                                width={400}
                                height={400}
                                className="rounded-lg object-cover"
                            />
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}
